.simple-cart-popup-button {
  position: fixed;
  z-index: 999;
  width: 60px;
  height: 60px;
  bottom: 20px;
  right: 20px;
  border-radius: 50%;
  background: #96588a;
  color: #fff;
  padding: 10px;
  margin: 0;
  cursor: pointer;
  border: none;
  font-size: 14px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
  outline: 0; }
  .simple-cart-popup-button:focus {
    outline-color: transparent; }
  .simple-cart-popup-button:focus, .simple-cart-popup-button:hover {
    background: #96588a;
    color: #f7f7f7; }
  .simple-cart-popup-button .simple-cart-popup-button-icon {
    width: auto;
    height: 20px;
    margin: 10px auto;
    display: block;
    transform: scale(1);
    transition: transform .2s ease; }
  .simple-cart-popup-button.opening .simple-cart-popup-button-icon-close {
    transform: scale(1); }
  .simple-cart-popup-button.opening .simple-cart-popup-button-icon-cart {
    transform: scale(0); }
  .simple-cart-popup-button.closing .simple-cart-popup-button-icon-close {
    transform: scale(0); }
  .simple-cart-popup-button.closing .simple-cart-popup-button-icon-cart {
    transform: scale(1); }
  .simple-cart-popup-button.active .simple-cart-popup-button-icon-cart {
    display: none;
    transform: scale(0); }
  .simple-cart-popup-button:not(.active) .simple-cart-popup-button-icon-close {
    display: none;
    transform: scale(0); }

.simple-cart-popup .simple-cart-button {
  display: block;
  border-radius: 5px;
  background: #96588a;
  color: #fff;
  text-align: center;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  text-decoration: none;
  height: 40px;
  line-height: 40px; }
  .simple-cart-popup .simple-cart-button:visited {
    color: #fff;
    background: #96588a;
    text-decoration: none; }
  .simple-cart-popup .simple-cart-button:hover, .simple-cart-popup .simple-cart-button:focus, .simple-cart-popup .simple-cart-button:active {
    color: #fff;
    background: #56324f;
    text-decoration: none; }

.simple-cart-popup-button .simple-cart-popup-button-actions span {
  left: 0;
  top: 0;
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
  color: #96588a;
  background: #fff;
  border: 1px solid #96588a;
  line-height: 18px; }

@keyframes simpleCartPopupShow {
  0% {
    opacity: 0;
    width: 90%; }
  100% {
    opacity: 1;
    width: 90%;
    transform: translate3d(0px, 0px, 0px); } }

@keyframes simpleCartPopupHide {
  0% {
    opacity: 1;
    width: 90%;
    transform: translate3d(0px, 0px, 0px); }
  100% {
    opacity: 0;
    width: 90%; } }

.simple-cart-popup {
  box-sizing: border-box;
  width: 0;
  max-width: 360px;
  min-height: 200px;
  height: 100%;
  max-height: calc( 90% - 100px);
  bottom: 100px;
  position: fixed;
  right: 20px;
  z-index: 9999;
  background: white;
  color: black;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  opacity: 0;
  transform: translate3d(10px, 10px, 0px);
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif; }
  .simple-cart-popup.opening {
    -webkit-animation-name: simpleCartPopupShow;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s;
    /* Safari 4.0 - 8.0 */
    animation-name: simpleCartPopupShow;
    animation-duration: 1s; }
  .simple-cart-popup.closing {
    -webkit-animation-name: simpleCartPopupHide;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s;
    /* Safari 4.0 - 8.0 */
    animation-name: simpleCartPopupHide;
    animation-duration: 1s; }
  .simple-cart-popup.open {
    opacity: 1;
    width: 90%;
    transform: translate3d(0px, 0px, 0px); }
  .simple-cart-popup * {
    box-sizing: border-box; }
  .simple-cart-popup a {
    color: #96588a; }
    .simple-cart-popup a:hover, .simple-cart-popup a:focus, .simple-cart-popup a:active, .simple-cart-popup a:visited {
      color: #96588a;
      text-decoration: underline; }
  .simple-cart-popup .simple-cart-popup-container {
    position: relative;
    height: 100%; }
  .simple-cart-popup .simple-cart-popup-body {
    height: calc( 100% - 40px);
    padding-bottom: 40px;
    overflow: auto;
    padding: 15px; }

.simple-cart-powered-by  {
  position: absolute;
  width: 100%;
  bottom: 0;
  color: #96588a;
  background: #f7f7f7;
  text-align: center;
  bottom: 0;
  left: 0;
  height: 40px;
  line-height: 40px;
  font-size: 14px; }

.simple-cart-popup-empty {
  text-align: center;
  margin: 40px 0; }
  .simple-cart-popup-empty svg {
    height: 60px;
    opacity: 0.5; }

.simple-cart-popup-table td,
.simple-cart-popup-table th {
  padding: 10px;
  vertical-align: middle;
  margin: 0;
  text-align: left;
  word-break: normal;
  border: none; }

.simple-cart-popup-table .sc-cart-popup-table-column.sc-cart-popup-table-image {
  width: 50px; }

.simple-cart-popup-table .sc-cart-popup-table-column.sc-cart-popup-table-total {
  text-align: right; }

.simple-cart-popup-table .sc-cart-popup-table-column.sc-cart-popup-table-quantity {
  text-align: center;
  width: 45px; }
  .simple-cart-popup-table .sc-cart-popup-table-column.sc-cart-popup-table-quantity input {
    width: 45px;
    border: 1px solid #e6e6e6;
    background: #f7f7f7;
