    line-height: 30px;
    padding: 0 5px;
    margin: 0; }

.simple-cart-popup-table .sc-cart-popup-table-footer-total {
  font-weight: 600;
  border-top: 1px solid rgba(0, 0, 0, 0.5); }
  .simple-cart-popup-table .sc-cart-popup-table-footer-total.sc-cart-popup-table-footer-total-value {
    text-align: right; }

.simple-cart-loading {
  position: relative; }
  .simple-cart-loading:before {
    content: '';
    z-index: 99;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.05); }
  .simple-cart-loading:after {
    position: absolute;
    z-index: 90;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    border-radius: 50%;
    border: 5px solid #96588a;
    border-color: #96588a #fff #96588a #fff;
    animation: simple-cart-dual-ring 1.2s linear infinite; }

@keyframes simple-cart-dual-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
